.header {
  min-height: 94vh;
  -ms-flex-align: center;
      align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.header h1 {
  margin: 0.5em;
  font-size: 2.25em;
  text-transform: uppercase;
}

.header p {
  font-size: 1.35em;
}

.header .container {
  text-align: center;
  margin-top: -3em;
  text-shadow: 0px 0px 3px #000;
  padding: 1em 1em 2.5em 1em;
  background-color: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-height: 700px) {
  .header .container {
    margin-top: 0;
  }
}

@media only screen and (min-width: 639px) and (max-width: 1099px) {
  .header h1 {
    font-size: 3em;
  }
  .header .container {
    margin-top: -7em;
  }

  .header .container {
    width: 75%;
  }
}

@media only screen and (min-width: 1100px) {
  .header h1 {
    font-size: 3em;
  }

  .header .container {
    margin-top: -7em;
  }

  .header .container {
    width: 50%;
  }
}

@media only screen and (min-width:  900px) {
  .header {
    background-attachment: fixed;
  }
}



